@import "../../style/sass/mixins";
.cat-nav-container {
  background-color: #1e1e1e;
  max-width: 100%;
  border-top: #444444 solid 1px;

  .list-items {
    list-style-type: none;
    display: inline-block;
    margin: 0.2em 1em;
    padding: 0.4em;
    height: 2.5em;

    a {
      text-decoration: none;
      color: white;
      height: 1.8em;
      font-size: 0.9em;
      font-weight: 700;

      &::after {
        content: "";
        display: block;
        bottom: 0;
        left: 0;
        height: 0.2em;
        background-color: red;
        transform: scaleX(0);
        @include transition(transition, 0.2s);
      }
      &:hover {
        &::after {
          transform: scaleX(1.1);
        }
      }
    }
  }
}
